import FormGenerator, { FormItem, IFormElm } from "ccvfj-react-app";
import { Fragment, useState } from "react";
import { FormValueEditor } from "../../types";
import { IconButton } from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";

interface FormViewProps {
  form: IFormElm[];
}

export const useFormView = () => {
  const [formElms, setFormElms] = useState<IFormElm[]>([]);

  const deleteElm = (index: number) => {
    setFormElms((prev) => prev.filter((_, i) => i !== index));
  };

  const onCbForm = (values: FormValueEditor) => {
    let options: any[] = [];
    if (values.componentName.toLowerCase() === "select" && values["option"]) {
      for (const key of Object.keys(values["option"])) {
        const optionName = key.split("name-")[1];
        if (optionName) {
          options.push({
            name: values["option"]["name-" + optionName],
            label: values["option"]["label-" + optionName],
          });
        }
      }
    }

    setFormElms((prev) => [
      ...prev,
      {
        inputProps: {
          componentName: values.componentName,
          options: options.length > 0 ? options : undefined,
        },
        name: values.name,
        col: {
          lg: values["col-lg"],
          md: values["col-md"],
          sm: values["col-sm"],
          xs: values["col-xs"],
        },
        label: values.label,
      },
    ]);
  };
  return {
    onCbForm,
    viewr: (
      <FormView
        form={formElms.map((item, index) => ({
          ...item,
          append: [
            {
              col: "auto",
              inputProps: {
                componentName: "custom",
                render: () => (
                  <IconButton
                    color="primary"
                    onClick={() => deleteElm(index)}
                    size="large"
                  >
                    <DeleteIcon fontSize="inherit" />
                  </IconButton>
                ),
              },
              name: "",
            },
          ],
        }))}
      />
    ),
    formElms,
  };
};

export const FormView = ({ form }: FormViewProps) => {
  const initialValues = form.reduce((p, elm) => {
    return {
      ...p,
      [elm.name]: "",
    };
  }, {});

  return (
    <Fragment>
      <header className="mt-5">
        <h2 className="text-center">View Form</h2>
      </header>
      <FormGenerator
        initialValues={initialValues}
        onSubmit={(_, { setSubmitting }) => {
          setSubmitting(false);
        }}
        listForm={[
          {
            title: "",
            form,
          },
        ]}
      />
    </Fragment>
  );
};
