import React, { Fragment, useEffect, useState } from "react";
import FormGenerator, {
  FormItem,
  IFormConfig,
  IGeneratorForm,
} from "ccvfj-react-app";
import { FormikValues } from "formik";
import { FormValueEditor, OnCbForm } from "../../types";
import * as Yup from "yup";
import Button from "@mui/material/Button";

interface FormEditorProps {
  onCbForm: OnCbForm;
}
export const FormEditor = ({ onCbForm }: FormEditorProps) => {
  const onSubmit: IFormConfig<FormValueEditor>["onSubmit"] = (
    values,
    { setSubmitting }
  ) => {
    onCbForm(values);

    setSubmitting(false);
  };
  return (
    <Fragment>
      <header className="mt-5">
        <h2 className="text-center">Editor</h2>
      </header>
      <FormGenerator<FormValueEditor>
        defaultComponentProps={{
          context: {
            btns: [
              ({ handleSubmit }: any) => (
                <Button
                  color="primary"
                  variant="contained"
                  fullWidth
                  type="submit"
                  className="mx-1"
                >
                  add
                </Button>
              ),
              ({ handleReset }: any) => (
                <Button
                  type="button"
                  onClick={handleReset}
                  fullWidth
                  variant="outlined"
                  color="primary"
                  className="mx-1"
                >
                  reset
                </Button>
              ),
            ],
          },
        }}
        validationSchema={Yup.object().shape({
          name: Yup.string().required(),
          componentName: Yup.string().required(),
          label: Yup.string().required(),
          ["option"]: Yup.lazy((values) => {
            if (!values) {
              return Yup.object().notRequired();
            }
            let obj = {};
            for (const key of Object.keys(values)) {
              // console.log({ key });
              obj = {
                ...obj,
                [key]: Yup.string().required(),
              };
              // const optionName = key.split("name-")[1];
              // if (optionName) {
              //   options.push({
              //     name: values["option"]["name-" + optionName],
              //     label: values["option"]["label-" + optionName],
              //   });
              // }
            }
            // console.log({ obj });

            return Yup.object(obj);
          }),
          "col-lg": Yup.number().max(12).min(1),
          "col-md": Yup.number().max(12).min(1),
          "col-sm": Yup.number().max(12).min(1),
          "col-xs": Yup.number().max(12).min(1),
        })}
        listForm={[
          {
            title: "ad",
            form: [
              {
                col: 12,
                inputProps: {
                  componentName: "input",
                },
                name: "name",
                label: "name",
              },
              {
                col: 12,
                inputProps: {
                  componentName: "select",
                  options: [
                    {
                      label: "Input",
                      name: "input",
                    },
                    {
                      label: "Select",
                      name: "select",
                    },
                  ],
                },
                name: "componentName",
                label: "name component",
              },
              {
                col: 12,
                inputProps: {
                  componentName: "repeater",
                  form: [
                    {
                      inputProps: {
                        componentName: "input",
                      },
                      name: "option.name",
                      label: "name",
                    },
                    {
                      inputProps: {
                        componentName: "input",
                      },
                      name: "option.label",
                      label: "label",
                    },
                  ],
                },
                name: "options-select",
                label: "options select",
                parents: {
                  type: "hide",
                  value: [["componentName", "select"]],
                },
              },
              {
                col: 12,
                inputProps: {
                  componentName: "input",
                },
                name: "label",
                label: "label",
              },
              {
                col: 3,
                inputProps: {
                  componentName: "input",
                  type: "number",
                },
                name: "col-lg",
                label: "col-lg",
              },
              {
                col: 3,
                inputProps: {
                  componentName: "input",
                  type: "number",
                },
                name: "col-md",
                label: "col-md",
              },
              {
                col: 3,
                inputProps: {
                  componentName: "input",
                  type: "number",
                },
                name: "col-sm",
                label: "col-sm",
              },
              {
                col: 3,
                inputProps: {
                  componentName: "input",
                  type: "number",
                },
                name: "col-xs",
                label: "col-xs",
              },
            ],
          },
        ]}
        onSubmit={onSubmit as any}
        initialValues={{
          name: "",
          componentName: "",
          label: "",
          // "col-lg": 12,
          // "col-md": 12,
          // "col-sm": 12,
          "col-xs": 12,
        }}
      />
    </Fragment>
  );
};
