import React, { Fragment, useState } from "react";
import { IFormRepeaterProps, IFormElm } from "ccvfj-react-app";
import Button from "@mui/material/Button";
import IconButton from "@mui/material/IconButton";
import DeleteIcon from "@mui/icons-material/Delete";
import AddIcon from "@mui/icons-material/Add";
const _toRe = (list: IFormElm[], index = 0) => {
  return list.map((item) => {
    const _item = Object.assign({}, item);
    _item.name += "-" + index;
    if (_item.prepend) {
      _item.prepend = _toRe(_item.prepend, index);
    }
    if (_item.append) {
      _item.append = _toRe(_item.append, index);
    }
    return _item;
  });
};
export const FormRepeater = ({
  list,
  component: Component,
  name,
  parents,
  ...useForm
}: IFormRepeaterProps) => {
  const [repeater, setRepeater] = useState<IFormElm[][]>([_toRe(list)]);

  const _addRow = () => {
    setRepeater((prev) => [...prev, _toRe(list, repeater.length)]);
  };

  const _deleteRow = (cIndex: number) => {
    setRepeater((prev) => prev.filter((item, index) => index !== cIndex));
  };
  return (
    <Fragment>
      {repeater.map((form, index) => {
        return (
          <div className="row gx-0 align-items-center" key={index}>
            <div className="col">
              <Component form={form} />
            </div>
            <div className="col-auto">
              {index >= 0 && (
                <IconButton
                  color="primary"
                  onClick={() => _deleteRow(index)}
                  size="large"
                >
                  <DeleteIcon fontSize="inherit" />
                </IconButton>
                // <Button
                //   onClick={() => _deleteRow(index)}
                //   type="button"
                //   variant="contained"
                // >
                //   delete
                // </Button>
              )}
            </div>
          </div>
        );
      })}
      <Button
        fullWidth
        color="primary"
        variant="contained"
        type="button"
        onClick={_addRow}
      >
        <AddIcon fontSize="medium" />
      </Button>
    </Fragment>
  );
};
